img {
	max-width: 100%;
}

/* Vars */
:root {
	--border-radius: 10px;
}

.bitcoin-rate,
.bitcoin-rate * {
	box-sizing: border-box;
}

.bitcoin-rate {
	margin: 0 35px;
	padding: 46px 83px 30px;
	max-width: 1000px !important;
	background-color: #f6f6f6;
	box-shadow: 0px 2px 10px #00000029;
	border-radius: calc(var(--border-radius) * 2);
	text-align: center;
	color: #000;
	font-family: "Segoe UI Regular", Arial, sans-serif;
}

h2.bitcoin-rate-title {
	line-height: 1.33;
	font-weight: 400;
	margin: 0 0 32px;
	font-size: 26px;
}

.bitcoin-rate-row {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	gap: 20px;
	margin-bottom: 30px;
}

.bitcoin-rate-item {
	border-radius: var(--border-radius);
	padding: 27px 25px 25px;
	width: min(100%, 194px);
	margin: 0 auto;
}
.bitcoin-rate-item.bitcoin-rate-item--usd {
	background-color: #81b562;
}
.bitcoin-rate-item.bitcoin-rate-item--gbp {
	background-color: #f0c9d2;
}
.bitcoin-rate-item.bitcoin-rate-item--eur {
	background-color: #9fcdcd;
}

.bitcoin-rate-item-value,
.bitcoin-rate-item-currency {
	display: block;
}

.bitcoin-rate-item-value {
	font-size: 30px;
	line-height: 1.33;
	margin-bottom: 4px;
}
.bitcoin-rate-item-value.up {
	color: #0f0;
}
.bitcoin-rate-item-value.down {
	color: #f00;
}

.bitcoin-rate-item-currency {
	font-size: 16px;
	line-height: 1.29;
}

.bitcoin-rate-fetch {
	line-height: 1.35;
	font-size: 14px;
	display: inline-block;
	width: min(100%, 194px);
	text-decoration: none;
	background-color: #fff;
	color: #000;
	border: 1px solid #707070;
	border-radius: var(--border-radius);
	padding: 10px;
	transition: box-shadow, background-color, color;
	transition-duration: 0.3s;
}
.bitcoin-rate-fetch:hover {
	box-shadow: 0px 5px 9px 0px rgba(0, 0, 0, 0.5);
}

/* Styles for width to 420px */
@media screen and (max-width: 420px) {
	.bitcoin-rate {
		padding-left: 30px;
		padding-right: 30px;
	}
}

/* Styles for width from 768px */
@media screen and (min-width: 768px) {
	.bitcoin-rate {
		padding: 50px 100px 32px;
	}

	h2.bitcoin-rate-title {
		margin: 0 0 40px;
		font-size: 59px;
	}

	.bitcoin-rate-row {
		gap: 30px;
		margin-bottom: 70px;
	}

	.bitcoin-rate-item {
		width: calc(100% / 3 - 60px / 3);
		padding: 27px 15px 29px;
	}

	.bitcoin-rate-item-value {
		/* font-size: 44px; */
		line-height: 1.31;
		margin-bottom: 0;
	}

	.bitcoin-rate-item-currency {
		font-size: 25px;
		line-height: 1.32;
	}

	.bitcoin-rate-fetch {
		min-width: 216px;
	}
}

/* Styles for width from 1024px */
@media screen and (min-width: 1024px){
	.bitcoin-rate-item-value{
		font-size: 44px;
	}
}
